html,
body {
  margin: 0;
  padding: 0;
}

body {
  font: 14px "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0 auto;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  font-weight: 300;
  background: linear-gradient(to right, #dfc4b2, #a4caa1, #9ee6b6);
}

.stopwatch {
  position: relative;
  border-radius: 2px;
}

