.watchText {
  width: 100%;
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgba(175, 47, 47, 0.15);
  text-rendering: optimizeLegibility;
  margin: 20px auto;
  color: #3cb6c3;
  font-family: "Playfair Display", serif;
  font-style: italic;
  letter-spacing: 3.4px;
  text-align: center;
  border: 1px solid;
  border-radius: 50px;
  margin-bottom: 80px;
}

.timer {
  background: #fff;
  width: 100%;
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgb(127, 127, 127);
  text-rendering: optimizeLegibility;
  box-shadow: 0px 9px 28px 17px rgba(35, 55, 206, 0.26);
  margin: 0 auto;
  border-radius: 50px;
  margin-bottom: 70px;
}

.stopwatch {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px 20px 0;
  font-size: 40px;
}
.control-panel__button {
  margin: auto;
  background: linear-gradient(to right, #a188fc 0%, #2eeade 50%, #a188fc 100%);
  background-size: 200%;
  background-position: 100%;
  color: #ccffff;
  padding: 1em 3em;
  border-radius: 2em;
  display: inline-block;
  box-shadow: 0 0 20px #229999;
  text-shadow: 0 0 5px #000;
  transition: all 0.7s;
  border: solid 1px #777f8b;
  cursor: pointer;
  margin-bottom: 8px;
  margin-right: 12px;
}
.control-panel__button:hover {
  background: linear-gradient(to right, #a188fc 0%, #2eeade 50%, #a188fc 100%);
  background-size: 200%;
  background-position: 0px;
  box-shadow: 0 0 40px #55ffff;
  color: #ccffff;
  text-shadow: 0 0 30px #000000;
  border: solid 1px #ccffff;
}

.hidden {
  display: none;
}
